@import url('https://fonts.googleapis.com/css2?family=Jost:wght@300;400;500&family=Open+Sans:wght@500;600;800&family=Work+Sans:wght@300;400;500&display=swap');

/* 
font-family: 'Jost', sans-serif;
font-family: 'Open Sans', sans-serif;
font-family: 'Work Sans', sans-serif;
 */
 
body,
html {
  border: 0;
  padding: 0;
  margin: 0;
  outline: 0;
  font-family: 'Poppins', sans-serif;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  transition: .2s all cubic-bezier(0.075, 0.82, 0.165, 1);
}

:root {
  --primary-link: #333;
  --primary-link-hover-background: #cfe2ff;
  --secondary-highlighter: #f7f7f7;
}





/* Banner */
#head {
	height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	background: rgba(0, 0, 0, 0.7) url("https://images.unsplash.com/photo-1461749280684-dccba630e2f6?w=700&auto=format&fit=crop&q=60&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxzZWFyY2h8OHx8d2Vic2l0ZSUyMHRlY2h8ZW58MHwwfDB8fHww");
	background-size: cover;
	background-attachment: fixed;
	background-position: center;
	background-blend-mode: overlay;
	color: rgb(255, 251, 251);
    /* margin-top: 100px; */
  }

.banner-content{
    align-items: center;
    text-align: center;
	justify-content: center;
  margin: 20px 10vw;


}

.banner-content h1{
	font-size: 54px;
	text-shadow: 1px 1px 1px rgba(0,0,0,0.1);
	font-weight: 900;
	margin-bottom: 20px;
	text-align: center;
    font-family: "Open sans", Helvetica, Arial;
}
.banner-content p{
	font-size: 20px;
	text-shadow: 1px 1px 1px rgba(0,0,0,0.1);
	font-weight: 400;
	text-align: center;
	margin-top: 30px;
}



.home_btn {
  padding: 10px;
  border: 2px solid #c17c0e;
  background: #c17c0e;
  color: #ffffff;
  border-radius: 25px;
  transition: all 0.2s;
  min-width: 180px;
font-size: 18px;
font-weight: 600;

}

.home_btn span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.2s;
}

.home_btn span:after {
  content: '\00bb';
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.2s;
  color: white;
}

.home_btn:hover span {
  padding-right: 25px;
}

.home_btn:hover span:after {
  opacity: 1;
  right: 0;
}